import testevents from './testevents';

const now = new Date("2025, 12, 03,");

var tas = [{ title: "" }, { diks: [{ id: "", name: "", tanassine: "" }] }];
var tid = [{ title: "" }, { diks: [{ id: "", name: "", tanassine: "" }] }];
var autoevents = [{
  id: "-1",
  title: "ⵉⵖⴼ ⵍ ⵎⵓⵍⵢ ",
  start: "",
  end: "",
  diks: [{ id: "", name: "", tanassine: "" }]

}];




for (let j = 0; j < 366; j++) {
  let kjam3 = parseInt(j / 17);
  let doura = parseInt(j / 22);
  let rjam3 = j % 17;


  if (kjam3 % 2 === 0) {

    if (rjam3 === 0) {
      // 4 aid illan j l jam3
      tas[j] = { title: "الجامع", diks: [parseInt((kjam3) % 4), parseInt(kjam3 + 1) % 4, parseInt(kjam3 + 2) % 4, parseInt(kjam3 + 3) % 4] };
      tid[j] = { title: "الجامع", diks: [parseInt((kjam3) % 4), parseInt(kjam3 + 1) % 4, parseInt(kjam3 + 2) % 4, parseInt(kjam3 + 3) % 4] };

    } else {

      const listeAs = [];
      let lengthas = parseInt(testevents[(2 * (j - kjam3) - 1) % 42].diks.length);
      for (let index = 0; index < lengthas; index++) {
        listeAs.push(parseInt(doura % lengthas + index) % lengthas);

      }
      const listeIdd = [];
      let lengthid = parseInt(testevents[(2 * (j - kjam3) - 2) % 42].diks.length);

      for (let index = 0; index < lengthid; index++) {
        listeIdd.push(parseInt(doura % lengthid + index) % lengthid);

      }

      if ((doura + kjam3) % 2 === 0) {






        tas[j] = { title: testevents[(2 * (j - kjam3) - 1) % 42].title, diks: listeAs };

        tid[j] = { title: testevents[(2 * (j - kjam3) - 2) % 42].title, diks: listeIdd };//

      } else {

        tas[j] = { title: testevents[(2 * (j - kjam3) - 2) % 42].title, diks: listeIdd };//

        tid[j] = { title: testevents[(2 * (j - kjam3) - 1) % 42].title, diks: listeAs };

      }





    }
  }

  else {
    if (rjam3 !== 0) {



              const listeAs = [];
              let lengthas = parseInt(testevents[(2 * (j - kjam3) - 2) % 42].diks.length);

              for (let index = 0; index < lengthas; index++) {
                listeAs.push(parseInt(doura % lengthas + index) % lengthas);

              }
          const listeIdd = [];
          let lengthid = parseInt(testevents[(2 * (j - kjam3) - 1) % 42].diks.length);
          for (let index = 0; index < lengthid; index++) {
            listeIdd.push(parseInt(doura % lengthid + index) % lengthid);

          }

      if ((doura + kjam3) % 2 === 0) {
              tas[j] = { title: testevents[(2 * (j - kjam3-1)) % 42].title, diks: listeAs };
              tid[j] = { title: testevents[(2 * (j - kjam3) - 1) % 42].title, diks: listeIdd };
            } else {
              tas[j] = { title: testevents[(2 * (j - kjam3) - 1) % 42].title, diks: listeIdd };
              tid[j] = { title: testevents[(2 * (j - kjam3-1) ) % 42].title, diks: listeAs };
            }

          } else {

            tas[j] = { title: "الجامع", diks: [parseInt((kjam3) % 4), parseInt(kjam3 + 1) % 4, parseInt(kjam3 + 2) % 4, parseInt(kjam3 + 3) % 4] };
            tid[j] = { title: "الجامع", diks: [parseInt((kjam3) % 4), parseInt(kjam3 + 1) % 4, parseInt(kjam3 + 2) % 4, parseInt(kjam3 + 3) % 4] };

          }


  }
  //

  now.setDate(now.getDate() + 1);

  autoevents.push({
    title: tid[j].title,
    start: now.setDate(now.getDate()),
    end: now.setDate(now.getDate()),
    diks: tid[j].diks

  },
    {
      title: tas[j].title,
      start: now.setDate(now.getDate()),
      end: now.setDate(now.getDate()),
      diks: tas[j].diks
    });


}








export default autoevents