import testevents from  './testevents'
//
//
import React from 'react';
import "./event.css"



 export  function DiksEvent(diksevent,title) {

var tiggoura = []
tiggoura=JSON.parse(JSON.stringify(testevents));
 var aittagourt =[{id:"",name:"",tanassine:""}];

 tiggoura.forEach((taggourte) => {
  if(taggourte.title===title){
    
    aittagourt=taggourte.diks;

  }
  
});
    return (
      <div > 
          {diksevent.map((e)=> {return(<li className='diksevent' key={e} > Imswi(ⵉⵎⵙⵡⵉ) {e} : 
          {aittagourt.map((t)=>t.id===e?t.name:"")}

          </li>)})}
          
          </div>)


    }