
import React from 'react';
import {DiksEvent} from './DiksEvent';
import ReactDOM from 'react-dom';

import "./event.css"

 export  function AghabdilEvent(event) {

const localDate = new Date(event.start).toLocaleDateString("fr");


    return (
      <div className='event'> 
          <h1>   Taggourt: {event.title}  Le :  {localDate}  </h1>
          <div id="details">

          </div >

          <button  className='btn'
           onClick={ () => { ReactDOM.render(DiksEvent(event.diks,event.title), document.getElementById('details')) }}>تفاصيل</button>
                


        </div>
    
    );
  

    }